const Types_1 = require("./Types");
//@ts-ignore
const genericOnClick = (info, tab) => {
    if (!tab || !info)
        return;
    const dataToSend = {
        message: JSON.stringify({ type: Types_1.EMessageTypes.none }),
        info: info,
        tabId: tab,
    };
    switch (info.menuItemId) {
        case "Create Note":
            dataToSend.message = JSON.stringify({ type: Types_1.EMessageTypes.create });
            sendMessage(dataToSend);
            break;
        case "overlay":
            dataToSend.message = JSON.stringify({ type: Types_1.EMessageTypes.overlay });
            sendMessage(dataToSend);
            break;
        case "sidebar":
            dataToSend.message = JSON.stringify({ type: Types_1.EMessageTypes.sidebar });
            sendMessage(dataToSend);
            break;
    }
};
